/*Button 스타일*/


/* GENERAL BUTTON STYLING */

button,
button::after {
    transition: all 1.0s;
}

button {
    background: none;
    width: 100px;
    padding: 10px;
    border: 3px solid #fff;
    border-radius: 5px;
    color: #fff;
    display: block;
    position: relative;
    text-transform: uppercase;
    z-index: 1;
    margin: 0 auto;
    margin-top: 10px;
}

button::before,
button::after {
    background: #fff;
    content: '';
    position: absolute;
    z-index: -1;
}

button:hover {
    color: #000;
}

.btn-style {
    overflow: hidden;
}

.btn-style::after {
    /*background-color: #f00;*/
    height: 100%;
    left: -50%;
    top: 0;
    transform: skew(50deg);
    transition-duration: 0.6s;
    transform-origin: top left;
    width: 0;
}

.btn-style:hover:after {
    height: 120%;
    width: 150%;
}


