/* 메뉴 부분 스타일 */

nav {
    width: 100%;
    position: relative;
}

.Menu {
    position: absolute;
    list-style-type: none;
    font-weight: bold;
    /*글자 굵기 */
    width: 100%;
    display: flex;
    right: 0;
    flex-flow: row nowrap;
    float: right;
}

.Menu li {
    border-radius: 1px solid #000;
    padding: 20px 0;
}

.Menu li a {
    display: inline-block;
    text-decoration: none;
    color: white;
    padding: 10px 20px;
    margin: 0 auto;
}

.Menu li a:hover {
    color: wheat;
}

.food_icon {
    margin-left: 10px;
}

.nature_icon {
    margin-left: 8px;
}

.SideMenu {
    position: fixed;
    border: 5px solid #000;
    border-radius: 30px;
    background: white;
    list-style-type: none;
    font-weight: bold;
    /*글자 굵기 */
    right: 10px;
    top: 200px;
    display: flex;
    flex-flow: column nowrap;
    z-index: 99;
    opacity: 0;
}

.SideMenu li {
    border-radius: 1px solid #000;
}

.SideMenu li a {
    display: inline-block;
    text-decoration: none;
    padding: 10px 20px;
    margin: 0 auto;
}

.SideMenu li a:hover {
    color: #f4f0e9;
}


/* 왼쪽 내용 부분 스타일 food */

.sub1 {
    display: flex;
    flex-flow: row nowrap;
    background-color: #423F3E;
    height: 1800px;
}

.gallary_section {
    display: flex;
    flex-flow: row wrap;
    width: 100%;
}

.gallary_section2 {
    width: 100%;
}

.box {
    border: 1px solid black;
    box-sizing: border-box;
    padding: 10px;
    margin: 3% 1.25% 3% 1.25%;
    width: 30%;
    height: 30%;
    /* background: url(); */
}

.gallary_list {
    list-style-type: none;
}

.gallary_list img {
    margin: 10px;
    float: left;
    position: static;
    width: 50%;
}

.about_area h2 {
    padding: 55px 0 105px;
    text-align: center;
    font-size: 40px;
    color: wheat;
}

.about_area .place_list {
    display: flex;
    flex-wrap: wrap;
}

.about_area .place_list li {
    float: left;
    width: 30%;
    margin-left: 2%;
    margin-bottom: 2%;
    text-align: center;
    position: relative;
    top: -50px;
}

.about_area .place_list li .img_topplace {
    width: 100%;
}

.about_area .place_list li h3 {
    font-weight: bold;
    font-size: 18px;
    padding-top: 25px;
    color: wheat;
}

.about_area .place_list li p.txt {
    box-sizing: border-box;
    padding: 10px 30px 0;
    width: 100%;
    overflow: hidden;
    word-break: keep-all;
    line-height: 1.8;
    color: rgb(190, 172, 137);
}

.about_area .place_list li .view {
    display: inline-block;
    margin-top: 20px;
    padding-top: 5px;
    border-top: 2px solid #333;
    color: rgb(190, 172, 137);
}


/* popup_base */

.popup_base {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    z-index: 200;
    display: none;
}

.popup_base .pop_content {
    position: relative;
    width: 580px;
    margin: 200px auto 0;
    box-sizing: border-box;
    background: #f4f0e9;
}

.pop_content .btn_xpop {
    position: absolute;
    right: 0;
    top: -55px;
}

.pop_content .btn_xpop a {
    display: block;
    width: 45px;
    height: 45px;
    text-align: center;
    text-indent: -9999px;
    overflow: hidden;
    background: url(../image/btn_xpop.png) no-repeat;
    background-size: 45px;
}

.pop_content .pop_list li {
    padding: 36px;
}

.pop_content .pop_list li+li {
    background: #f4f0e9;
}

.pop_content .pop_list li+li+li {
    background: #f4f0e9;
}

.pop_content .pop_list li h2.tit_pop {
    padding: 10px 0 24px;
    font-weight: bold;
    font-size: 30px;
    letter-spacing: -2px;
}

.pop_content .pop_list li img.pop_img {
    width: 100%;
}

.pop_content .pop_list li .pop_txt {
    padding-top: 15px;
    display: flex;
}

.pop_content .pop_list li .pop_txt p {
    padding-top: 10px;
    line-height: 1.8;
    font-size: 17px;
    width: 95%;
    margin: 0 auto;
    overflow: hidden;
    word-break: keep-all;
    letter-spacing: -2px;
}

.pop_content .pop_list li .pop_txt .Info1 {
    width: 30%;
}

.pop_content .pop_list li .pop_txt .Info2 {
    width: 70%;
    position: relative;
}

.pop_content .pop_list li .pop_txt .Info2 span {
    display: inline-block;
    position: absolute;
    right: 0;
}


